import { Component, OnInit, TemplateRef, ViewChild,AfterViewInit, ChangeDetectorRef, HostListener, SimpleChange } from '@angular/core';
import { FormGroup, UntypedFormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { Configuration } from 'src/app/app.configuration';
import { CrudHttpService } from 'src/app/crud-http.service';
import { DataService } from 'src/app/shared/services/data-service';
import { StorageConfiguration } from 'src/app/shared/storage-setting/storage-configuration';
import { GroupDetailsComponent } from './group-details.component';

@Component({
  selector: 'app-group-list',
  templateUrl: './group-list.component.html',
  styleUrls: ['./group-list.component.scss']
})
export class GroupListComponent implements OnInit {
  @ViewChild('trailperiod', { static: true }) trailperiod: TemplateRef<any>;

  roleName: any;
  groupUserList = []
  displayedColumns: string[] = ['position', 'groupName', 'createdBy', 'createdDate', 'groupOrganization', 'feedbackDueDate', 'questionPDF', 'actions'];
  pageNumber = 1;
  todoList: any = [];
  filteredData = [];
  public dataList: any[]
  groupSearchValue: '';
  categoryId: any;
  membersForm: FormGroup;
  groupListPayload: { createdBy_Id: any; createdDate: any; feedbackDueDate: any; groupName: any; };
  apiUrl: string;

  
  totalRecords = 0;
  pageSize = 10;
  currentPage = 0;
  pageIndex=  0;
  tempGroupUserList: any[];
  noResultsFound: boolean = false;


  constructor(
    private router: Router,
    private crudHttpService: CrudHttpService,
    private storageConfiguration: StorageConfiguration,
    private spinner: NgxSpinnerService,
    private dialog: MatDialog,
    private dataService: DataService,
    private configuration: Configuration,
    private cdr: ChangeDetectorRef,
    private toastr: ToastrService,
  ) {
    this.dataService.getPopupdata.subscribe(res => {
     
      if(res)
      this.getGroupList();
    })
   }

  ngOnInit(): void {
    this.roleName = this.storageConfiguration.sessionGetItem(this.storageConfiguration.role);
    this.getGroupList();

  }
  
  ngAfterViewInit(): void{
  this.getGroupList();
}


// ngDoCheck() {
//   this.getGroupList();
// }

  getGroupList(pageNation?) {
    this.spinner.show();

    let apiUrl = this.configuration.group;
    if (typeof pageNation === 'number' && pageNation > 0) {
      apiUrl += `?pageNumber=${pageNation}`;
    }
    this.dataService.getGroupList(apiUrl).subscribe((response: any) => {
      console.log('get', response);
      this.spinner.hide();
      this.groupUserList = response?.data.reverse(); 
      
           
      this.tempGroupUserList = this.groupUserList;
      this.totalRecords = response.totalRecords;
      this.pageSize = response.pageSize;
      this.currentPage = response.currentPage;
    }, (error) => {
      this.spinner.hide();

    });
    this.cdr.detectChanges();
  }

  trackByFn(index: number, item: any) {
    return item._id; // Replace with a unique identifier property in your data
  }

  GroupListTable(categoryList) {
    let groupListTable = [];
    categoryList.forEach((element, index) => {
      groupListTable.push({
        id: element._id,
        position: (index + 1) + (this.pageNumber - 1) * 10,
        groupName: element.type,
        createdBy_Id: element.createdBy_Id,
        createdDate: element.createdDate,
        tempalte_id: element.tempalte_id ? element.tempalte_id : '',
        feedbackDueDate: element.feedbackDueDate,
        isActive: element.isActive,
        isDeleted: element.isDeleted
      });
    });

    return groupListTable;
  }


  onPageChanges(event: any) {
    this.pageIndex = event.pageIndex + 1
    this.getGroupList(this.pageIndex);
  }


  grouplistTodos() {
    this.crudHttpService.listed().subscribe((response) => {
      this.todoList = response;
    }, (error) => {
    });
  }

  getgroupUserList() {
    this.crudHttpService.listed().subscribe((data: any) => {
      this.filteredData = data;
      // this.groupUserList = [...this.filteredData];
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }


 

  openAddUserPopup(): void {
    const dialogRef = this.dialog.open(GroupDetailsComponent, {
      width: '1146px',
    });
    if (this.roleName === 'super-admin' || this.roleName === 'admin') {
      this.router.navigate(['/map/groups'], { queryParams: { isEdit: false, isSuperAdmin: true } });
    }
    dialogRef.afterClosed().subscribe(result => {
     
    });
  }

  


  filterData() {
    
    if (this.groupSearchValue != '') {
      this.groupUserList = this.tempGroupUserList.filter((item) => {
        return item.groupName.toLowerCase().includes(this.groupSearchValue.toLowerCase());
      });
  
      if (this.groupUserList.length === 0) {
        this.noResultsFound = true;
      } else {
        this.noResultsFound = false;
      }
    } else {
      this.groupUserList = [...this.tempGroupUserList];
      this.noResultsFound = false;
    }
  }


  resetFilter() {
    this.groupSearchValue = '';
    this.groupUserList = [...this.filteredData];
  }

  edituser(id,item) {
   
    this.router.navigate(['/map/groups/group-details'], { queryParams: { ID: id, isEdit: true } } );
  }
  

  edituserdetail(id) {
   
    const indexToUpdate = this.groupUserList.findIndex((ele) => ele._id === id);
   
    const filterData = this.groupUserList.filter(i => i._id == id);
    
    
    // if (indexToUpdate !== -1) {
    //   this.groupUserList[indexToUpdate] = {
    //     ...this.groupUserList[indexToUpdate],
    //     // createdBy_Id: this.groupUserList.createdBy_Id,
    //     // createdDate: this.groupUserList.createdDate,
    //     // feedbackDueDate: this.groupUserList.feedbackDueDate,
    //     // groupName: this.groupUserList.groupName,
    //   };
    // }

    const payload = {
         createdBy_Id: filterData[0].createdBy_Id,
        createdDate: filterData[0].createdDate,
        feedbackDueDate: filterData[0].feedbackDueDate,
        groupName: filterData[0].groupName,
        groupOranization_id: filterData[0].groupOranization_id,
        questionPDF: filterData[0].tempalte_id
    }
    this.dataService.updateGroups(this.configuration.group, id, payload).subscribe((data: any) => {
      this.dataList = data;
      
      
    });
  }
  
  

  categoryDelete(categoryId: number, content) {
    this.dialog.open(content, {
      width:'25%'
    });
    this.categoryId = categoryId
    
  }
  

  closeDialog() {
    this.dataService.deleteGroup(this.configuration.group, this.categoryId).subscribe((res) => {
     
      this.dialog.closeAll()
      this.getgroupUserList()
      this.getGroupList();
      this.toastr.success('Successfully Deleted')
    },
    );
  }

  cancelDialog() {
    this.dialog.closeAll()
  }

}
