import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Configuration } from './app.configuration';

@Injectable({
  providedIn: 'root'
})
export class MembersService {

  updateUserInTable(userDetails: any) {
    throw new Error('Method not implemented.');
  }
  updateUserDetails(userDetails: any) {
    throw new Error('Method not implemented.');
  }
  getUserDetails(userId: any) {
    throw new Error('Method not implemented.');
  }

  apiURL: string = '' ;
  headers = new HttpHeaders().set('Content-Type', 'application/json');
  // getItemUrl: string = 'http://192.168.101.175:3000/api/v1/group';

  constructor(private http: HttpClient , public configuration: Configuration) { 
    this.apiURL = this.configuration.members
  }

  createMembers(data: any): Observable<any>{
    let API_URL = `${this.apiURL}`+ `/bulkCreate`;
    return this.http.post(API_URL, data)
      .pipe(
        catchError(this.handleError)
      )
  }
  delete(id: any): Observable<any> {
    var API_URL = `${this.apiURL}/${id}`;
    return this.http.delete(API_URL).pipe(
      catchError(this.handleError)
    )
  }
  getMemberById(id: number): Observable<any> {
    return this.http.get<any>(`${this.apiURL}/${id}`);
}

  memberListed(){
    return this.http.get(`${this.apiURL}`)
  }
  listed(){
    return this.http.get(`${this.apiURL}`)
  }

  membersUpdate(id: any, data: any): Observable<any>{
    const API_URL = `${this.apiURL}/${id}`;
    return this.http.put(API_URL, data, { headers: this.headers }).pipe(
      catchError(this.handleError)
    )
  }

  handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      console.error('An error occurred:', error.error.message);
    } else {
      console.error(
        `Backend returned code ${error.status}, ` +
        `body was: ${error.error}`);
    }
    return throwError(
      'Something bad happened; please try again later.');
  };

  getMemberData(): Observable<any> {
    return this.http.get(`${this.apiURL}`)
  }

}
