import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { GroupListComponent } from './group-list.component';
import { GroupDetailsComponent } from './group-details.component';
import { UnoMembersComponent } from './uno-members.component';
import { HowardMembersComponent } from './howard-members.component';
import { AddMemberComponent } from './add-member.component';
import { ResponsePdfComponent } from './response-pdf.component';

const routes: Routes = [
  {
    path:'', 
    component: GroupListComponent
  },
  {
    path: 'group-details',
    component: GroupDetailsComponent
  },
  {
    path: 'uno-members',
    component: UnoMembersComponent
  },
  {
    path: 'howard-members',
    component: HowardMembersComponent
  },
  {
    path: 'add-member',
    component: AddMemberComponent
  },
  {
    path:'response-pdf',
    component:ResponsePdfComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class GropusRoutingModule { }
