import { Component, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Configuration } from 'src/app/app.configuration';
import { DataService } from 'src/app/shared/services/data-service';

@Component({
  selector: 'app-uno-members',
  templateUrl: './uno-members.component.html',
  styleUrls: ['./uno-members.component.scss']
})
export class UnoMembersComponent {

  respondentForm: FormGroup;
  Category = [
    { value: 'Peer', viewValue: 'Peer' },
    { value: 'directReporter', viewValue: 'Direct Reporter' }
  ];
  orgName: any;
  quesTemplate:any;
  tempName: any;
  participantID: any;
  checkboxValue:boolean = false;
  isEdit: any;

  constructor(private fb: FormBuilder, private dialog: MatDialog, public activeRouter : ActivatedRoute,private configuration:Configuration,private dataService:DataService,
    public toastr: ToastrService,
    @Inject(MAT_DIALOG_DATA) public data: { memberId: any }

    ){ 
    this.respondentForm = this.fb.group({
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      title: ['', Validators.required],
      organization: ['', Validators.required],
      email: ['', Validators.required],
      mobile: ['', Validators.required],
      questionTemplate: ['', Validators.required],
      roles: ['', Validators.required]    
    });
  }
  ngOnInit(): void {
    let apiUrl = this.configuration.survey
    let membersUrl = this.configuration.members
    this.activeRouter.queryParams.subscribe((queryParams) => {
      this.orgName = queryParams.OrgName
      this.quesTemplate = queryParams.temp
      this.participantID = queryParams.ID
      this.isEdit = queryParams.isEdit
    });
    this.dataService.getById(apiUrl,this.quesTemplate).subscribe((data:any)=>{
      this.tempName =data.data.temple.templateName
      this.patchData()
    })
   if(this.data?.memberId){
    this.dataService.getById(membersUrl,this.data.memberId).subscribe((data:any)=>{
     
    this.patchFields(data)
      
    })
   }
  }
  patchFields(data:any){
    this.respondentForm.get('firstName').patchValue(data?.data?.firstName),
    this.respondentForm.get('lastName').patchValue(data?.data?.lastName),
this.respondentForm.get('email').patchValue(data?.data?.email),
 this.respondentForm.get('mobile').patchValue(data?.data?.mobile),
this.respondentForm.get('title').patchValue(data?.data?.title)
if(data?.data?.catogory === `Supervisor`){
  this.checkboxValue = true
  this.Category.push({value:'Supervisor', viewValue:'SuperVisor'})
  this.respondentForm.get('roles').patchValue(data?.data?.catogory)
}
else{
  this.respondentForm.get('roles').patchValue(data?.data?.catogory)
}


  }
  cancel(){
    this.dialog.closeAll()
  }

  onCheckboxChange(event?){
    this.checkboxValue = event.checked
    if(this.checkboxValue){
      const valueToCheck = { value: 'Supervisor', viewValue: 'SuperVisor' };
    const exists = this.Category.some(item => {
  return item.value === valueToCheck.value && item.viewValue === valueToCheck.viewValue;
});
if (!exists) {
  this.Category.push({value:'Supervisor', viewValue:'SuperVisor'}) 
}   
  }
    else{
     let index = this.Category.indexOf({value:'Supervisor', viewValue:'SuperVisor'})
     this.Category.splice(index,1)
}
   
  }
  
  patchData(){
    this.respondentForm.get('organization').setValue(this.orgName)
    this.respondentForm.get('organization').disable();
    this.respondentForm.get('questionTemplate').setValue(this.tempName)
    this.respondentForm.get('questionTemplate').disable();
  }

  keyPress(event: any) {
    const pattern = /[0-9\+\ ]/;
    let inputChar = String.fromCharCode(event.charCode);
    if (event.keyCode != 8 && !pattern.test(inputChar)) {
      event.preventDefault();
    }
  }

  onSubmit(){

    
    let apiURl = this.configuration.members
    let payLoad = {
      firstName: this.respondentForm.get('firstName').value,
    lastName: this.respondentForm.get('lastName').value,
    email:this.respondentForm.get('email').value,
    mobile:this.respondentForm.get('mobile').value,
    participant_id:this.participantID,
    template_id: this.quesTemplate,
    title:this.respondentForm.get('title').value,
    catogory: this.respondentForm.get('roles').value
    }
    if(this.data?.memberId){
      this.dataService.update(this.data.memberId,apiURl,payLoad).subscribe((data:any)=>{
        this.toastr.success('Respondent Updated Succesfully')
        this.cancel()
      },(err)=>{
        this.toastr.error('Something went wrong')
      })
    }
    else{
      this.dataService.addParticipant(apiURl,payLoad).subscribe((data:any)=>{
        this.toastr.success('Respondent Created Succesfully')
        this.cancel()
      },(err)=>{
        this.toastr.error('Please Required The Field')
      })
    }
  
  }
}
