import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { GropusRoutingModule } from './gropus-routing.module';
import { GroupListComponent } from './group-list.component';
import { GroupDetailsComponent } from './group-details.component';
import { UnoMembersComponent } from './uno-members.component';
import { HowardMembersComponent } from './howard-members.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatIconModule } from '@angular/material/icon';
import { MaterialModule } from 'src/material.module';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatButtonModule } from '@angular/material/button';
import { AddMemberComponent } from './add-member.component';
import {MatStepperModule} from '@angular/material/stepper';
import { QuestionsPdfComponent } from './questions-pdf.component';
import { RespondantPdfComponent } from './respondant-pdf/respondant-pdf.component';
import { AggregatePdfComponent } from './aggregate-pdf/aggregate-pdf.component';
import { NgxMaskModule, IConfig } from 'ngx-mask'
import { NgxSpinnerModule } from 'ngx-spinner';

const maskConfig: Partial<IConfig> = {
  validation: false,
};
@NgModule({
  declarations: [
    GroupListComponent,
    GroupDetailsComponent,
    UnoMembersComponent,
    HowardMembersComponent,
    AddMemberComponent,
    QuestionsPdfComponent,
    RespondantPdfComponent,
    AggregatePdfComponent,
  ],
  imports: [
    GropusRoutingModule,
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    MatIconModule,
    MaterialModule,
    MatToolbarModule,
    MatFormFieldModule,
    MatInputModule,
    MatButtonModule,
    NgxSpinnerModule,
    MatStepperModule,
    NgxMaskModule.forRoot(maskConfig),
  ],
  exports: [NgxSpinnerModule],
})
export class GropusModule { }
